import request from '../helpers/request';
import { fetchDownloadsCount } from './sourceforge';
import { humanDate, humanSize } from '../helpers/utils';

const baseURL = 'https://raw.githubusercontent.com/Herna1994';

const fetchDevices = async () => {
  try {
    const res = await request(`${baseURL}/ota_test/master/devices`);

    const brands = [];
    const devices = [];

    res.devices.forEach(device => !brands.includes(device.manufacturer) && brands.push(device.manufacturer));

    brands.forEach(brand => devices.push({
      name: brand,
      devices: res.devices.filter(device => device.manufacturer === brand),
    }));

    return devices.sort((a, b) => a.name.localeCompare(b.name))
  } catch (e) {
    console.log('devices fetch failed');
  }
};

const fetchBuilds = async (codename, variant) => {
  try {
    const res = await request(`${baseURL}/ota_test/master/updates/${codename}`);

    const filteredArray = res.response.filter(response => response.romtype === variant);

    const promises = filteredArray.map(async (build) => {
      const downloads = await fetchDownloadsCount(build.filename, build.version, variant, codename);
      const changelog = await fetchChangelog(variant, codename) || "";

      return {
        ...build,
        size: humanSize(build.size),
        datetime: humanDate(build.datetime),
        md5: build.id,
        downloads,
        changelog,
      };
    }).reverse();

    return await Promise.all(promises);
  } catch (e) {
    return [];
  }

};

const fetchChangelog = async (variant, codename) => {
  try {
    const res = await request(`${baseURL}/ota/11/${codename}/changelogs_${variant}.txt`, false);

    return res.includes('404') ? 'Changelog data no found' : res;
  } catch (err) {
    return 'Changelog data no found';
  }
};

const fetchROMChangelog = async () => {
  const res = await request('https://raw.githubusercontent.com/rajkale99/OTA/11/rom_changelog.txt', false);
  return res;
};

export { fetchDevices, fetchBuilds, fetchROMChangelog };
