import request from '../helpers/request';
import { getToday, getTimestamp } from '../helpers/utils';

const baseURL = 'https://sourceforge.net/projects/aospa';

const fetchDownloadsCount = async (filename, version, romtype, codename) => {
  try {
    const res = await request(`${baseURL}/files/${version}/${romtype}/${codename}/${filename}/stats/json?start_date=2019-04-06&end_date=${getToday()}`);
    return res.total;
  } catch (err) {
    return 0;
  }
};

const generateDownloadURL = (filename, version, romtype, codename) => {
  const downloadBase = `https://sourceforge.net/projects/aospa//files/${version}/${romtype}/${codename}/${filename}`;
  return `${downloadBase}`;
};

export { fetchDownloadsCount, generateDownloadURL };
